function scrollTo(e){
  e.preventDefault()
  document.querySelector(e.target.dataset['url']).scrollIntoView({ behavior: "smooth" })
}

export default function Navigation() {
  return (
    <div className='navigation'>
      <a onClick={e => scrollTo(e)} data-url='#comparison'> /  </a>
      <a onClick={e => scrollTo(e)} data-url='#correlation'>  </a>
      <a onClick={e => scrollTo(e)} data-url='#details'>  </a>
      <a onClick={e => scrollTo(e)} data-url='#industrial'>  </a>
    </div>
  )
}
